import React, { Component } from 'react';
import { Modal, Button, Col, Row, Form, FormCheck } from "react-bootstrap";
import Social from './authorization/social/Social'
import LoginForm from './authorization/login/LoginForm'
import Timer from './timer/Timer'

// ... REDUX ...
import withReducer from "../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";

import Cookies from 'universal-cookie';

class OTPPopup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            msg:null,
            isActive : false,
            resetTime: null
        };
    }

    
    //... HANDLERS ...

    handleChangeRemember = () => {
        this.setState({ remember: !this.state.remember });
    }

    static getDerivedStateFromProps(props, state) {
        // console.log('User' + props.user);
        // if(props.user != null)
        // {
        //     console.log('msg :: ' + JSON.stringify(props));
        //     return props.onPopClicked();
        // }
        // else if(props.user == null)
        // {
          console.log('Number :: ' + props.number)
            return{
                msg: props.msg,
                show: props.otpDialog,
                otp_msg: props.otp_msg,
                otp_variant: props.otp_variant
            }
        // }
    }    

    handleCodeChange = (evt) => {
      if(evt.target.value && Object.keys(evt.target.value).length === 4)
      {
        this.props.verifyOTP(evt.target.value, this.props.number, this.props.location && this.props.location.pathname)
      }
    }

    handleClose = () => {
      //eslint-disable-next-line
      if(confirm(`You would not be able to checkout as guest. You will need to wait for some time. Are you sure you want to go back ?`))
      {
        let cookie = new Cookies()
        let date = new Date()
        date.setTime(date.getTime() + (300 * 1000)); // for 5 minute...
        cookie.set('_otp__', '$svhweli42g09028b420kvsdddfsfr321111///@@!#!@dvvD_SDV_E#@f2rr3r23', {expires: date})

        this.props.closeOTP()
      }
    }

    handleResendOTP = () => {
      let formBody = new FormData()
      formBody.append('number', this.props.number)

      this.props.ResendOTP(formBody)
      this.setState({
        resetTime: 1,
        isActive: false
      })
    }

    setActive = () => {
      this.setState({
        isActive: true
      })
    }

    resetTimeNull = () => {
      this.setState({
        resetTime: null
      })
    }

  render() {
    return (
      <>
        <Modal
          className="cust-modal login-modal"
          show={this.state.show  && this.state.show}
          // onHide={this.props.closeOTP}
          size="md"
        >
          <div className="btn-close">
            <Button
              type="button" 
              className="close"
              onClick={this.handleClose}
            >
              <span aria-hidden="true">Ã—</span>
              <span className="sr-only">Close</span>
            </Button>
          </div>

          <Modal.Body>
                <Row className="m-0">
                    <Col md={12}>
                        <Form className="p-4">
                            <Form.Control
                                type="text"
                                maxlength="4"
                                name="otp"
                                placeholder="Enter 4 Digit Verification Code*"
                                className="my-3"
                                onChange={this.handleCodeChange}
                            /> 
                            {/* <Button className="">Login</Button> */}
                            <p style={{color:`${this.state.otp_variant}`, fontSize:'14px'}} >{this.state.otp_msg}</p>
                            <Button onClick={this.state.isActive && this.handleResendOTP} className={this.state.isActive ? "mx-1" : 'mx-1 resend-btn-off'}>Resend</Button>
                        </Form>
                    </Col>
                </Row>
                <Row className="m-0">
                  <Timer setActive={this.setActive} resetTime={this.state.resetTime} resetTimeNull={this.resetTimeNull}/>
                </Row>
                
          </Modal.Body>
        </Modal>
      </>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
    return {
        user: state.OTPPopup.auth.user,
        otpDialog: state.OTPPopup.auth.otpDialog,
        otp_variant: state.OTPPopup.auth.otp_variant,
        otp_msg: state.OTPPopup.auth.otp_msg
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators({
   closeOTP: Actions.closeOTP,
   verifyOTP: Actions.verifyOTP,
   ResendOTP: Actions.ResendOTP
  }, dispatch)
}

export default withReducer('OTPPopup', reducer)
                (
                    connect(mapStateToProps,mapDispatchToProps)(OTPPopup)
                )