import React, { Component, PropTypes } from 'react';

class AccountKitWeb extends Component {

  constructor(props) {
    super(props);
    this.state = {
      inited: false,
      appId: '',
      csrf: '',
      version: '',
      debug: true,
    }
  }

  componentWillMount() {
    const script = document.createElement("script");
    script.src = "https://sdk.accountkit.com/en_US/sdk.js";
    document.body.appendChild(script);
  }

  componentDidMount() {
    this.initAccountKit();
  }

  componentWillUpdate(nextProps, nextState) {
    if(!this.state.inited && nextState.inited) {
      window.AccountKit_OnInteractive = () => {
          window.AccountKit.init({
            appId: nextState.appId,
            state: nextState.csrf,
            version: nextState.version,
            debug: this.state.debug
        })
      }
    }
  }

  initAccountKit() {
        this.setState({
          inited:true,
          appId: '494340657971708',
          csrf: 'hahahahhaa',
          version: 'v1.1'
        });
  }

  render() {
    return null
  }

}

export default AccountKitWeb;