import React, { Component } from 'react'
import './Timer.css'
import moment from 'moment'

export default class Timer extends Component {
    
    constructor(props)
    {
        super(props)

        this.state = {
            countdown : `0`,
            isTimer: true
        }
    }

    componentDidMount() {
        let duration = moment.duration({
            'minutes' : 1,
            'seconds' : 0 
        })

        let timeStamp = new Date(0, 0, 0, 2, 10, 30)
        let timeInterval = 1

        let timer = setInterval(() => {
            timeStamp = new Date(timeStamp.getTime() + timeInterval * 1000);

            duration = moment.duration(duration.asSeconds() - timeInterval, 'seconds');
            var min = duration.minutes();
            var sec = duration.seconds();

            // sec -= 1;
            if (min < 0) return clearInterval(timer);
            if (min < 10 && min.length !== 2) min = '0' + min;
            if (sec < 0 && min !== 0) {
                min -= 1;
                sec = 59;
            } else if (sec < 10 && sec.length !== 2) sec = '0' + sec;

            this.setState({
                countdown: min + ':' + sec,
                min: min,
                sec: sec
            }, () => {
                if (parseInt(this.state.min) === 0 && parseInt(this.state.sec) === 0)
                {
                    clearInterval(timer);
                    // alert('triggered')
                    this.setState({
                        isTimer: false
                    }, () => {
                        this.props.setActive()
                    })
                }
            })

            
        }, 1000)

    }

    ResetTime = () => {
        this.setState({
            isTimer: true
        }, () => {
            let duration = moment.duration({
                'minutes' : 1,
                'seconds' : 0 
            })
    
            let timeStamp = new Date(0, 0, 0, 2, 10, 30)
            let timeInterval = 1
    
            let timer = setInterval(() => {
                timeStamp = new Date(timeStamp.getTime() + timeInterval * 1000);
    
                duration = moment.duration(duration.asSeconds() - timeInterval, 'seconds');
                var min = duration.minutes();
                var sec = duration.seconds();
    
                // sec -= 1;
                if (min < 0) return clearInterval(timer);
                if (min < 10 && min.length !== 2) min = '0' + min;
                if (sec < 0 && min !== 0) {
                    min -= 1;
                    sec = 59;
                } else if (sec < 10 && sec.length !== 2) sec = '0' + sec;
    
                this.setState({
                    countdown: min + ':' + sec,
                    min: min,
                    sec: sec
                }, () => {
                    if (parseInt(this.state.min) === 0 && parseInt(this.state.sec) === 0)
                    {
                        clearInterval(timer);
                        // alert('triggered')
                        this.setState({
                            isTimer: false
                        }, () => {
                            this.props.setActive()
                            this.props.resetTimeNull()
                        })
                    }
                })
    
                
            }, 1000)
        })

    }

    componentDidUpdate(prevProps, prevState) {
        if(this.props.resetTime && prevProps.resetTime !== this.props.resetTime)
        {
            this.ResetTime()
        }
    }
    
    render() {
        return (
            <div class={this.state.isTimer ? "countdown" : 'countdown timer-off'}>
                {this.state.countdown}
            </div>
        )
    }
}
